	module.exports = {"data":{"postsJson":{"title":"Preparing for the Mission in Galilee","questionsTitle":"HUNGERING TO KNOW GOD MORE","display":"block","bookName":"Luke","ref":"2:41-4:44","html":"<p>   <strong>2</strong><u>41</u>His parents went every year to Jerusalem at the feast of the Passover.<br />   <u>42</u>When he was twelve years old, they went up to Jerusalem according to the custom of the feast, <u>43</u>and when they had fulfilled the days, as they were returning, the boy Jesus stayed behind in Jerusalem. Joseph and his mother didn’t know it, <u>44</u>but supposing him to be in the company, they went a day’s journey, and they looked for him among their relatives and acquaintances. <u>45</u>When they didn’t find him, they returned to Jerusalem, looking for him. <u>46</u>After three days they found him in the temple, sitting in the middle of the teachers, both listening to them, and asking them questions. <u>47</u>All who heard him were amazed at his understanding and his answers. <u>48</u>When they saw him, they were astonished, and his mother said to him,</p><h1>MARY MOTHER OF JESUS</h1><h4>Son, why have you treated us this way? Behold, your father and I were anxiously looking for you.</h4><p>   <u>49</u>He said to them,</p><h1>JESUS</h1><h2>Why were you looking for me? Didn’t you know that I must be in my Father’s house?</h2><p><u>50</u>They didn’t understand the saying which he spoke to them. <u>51</u>And he went down with them, and came to Nazareth. He was subject to them, and his mother kept all these sayings in her heart. <u>52</u>And Jesus increased in wisdom and stature, and in favor with God and men.<br />   <strong>3</strong><u>1</u>Now in the fifteenth year of the reign of Tiberius Caesar, Pontius Pilate being governor of Judea, and Herod being tetrarch of Galilee, and his brother Philip tetrarch of the region of Ituraea and Trachonitis, and Lysanias tetrarch of Abilene, <u>2</u>in the high priesthood of Annas and Caiaphas, the word of God came to John, the son of Zacharias, in the wilderness. <u>3</u>He came into all the region around the Jordan, preaching the baptism of repentance for remission of sins. <u>4</u>As it is written in the book of the words of Isaiah the prophet,<br />   The voice of one crying in the wilderness,<br />   ‘Make ready the way of the Lord.<br />   Make his paths straight.<br />   <u>5</u>Every valley will be filled.<br />   Every mountain and hill will be brought low.<br />   The crooked will become straight,<br />   and the rough ways smooth.<br />   <u>6</u>All flesh will see God’s salvation.’”*<br />   <u>7</u>He said therefore to the multitudes who went out to be baptized by him,</p><h1>JOHN THE BAPTIST</h1><h4>You offspring of vipers, who warned you to flee from the wrath to come? <u>8</u>Therefore produce fruits worthy of repentance, and don’t begin to say among yourselves, ‘We have Abraham for our father;’ for I tell you that God is able to raise up children to Abraham from these stones! <u>9</u>Even now the ax also lies at the root of the trees. Every tree therefore that doesn’t produce good fruit is cut down, and thrown into the fire.</h4><p>   <u>10</u>The multitudes asked him,</p><h1>THE CROWD</h1><h4>What then must we do?</h4><p>   <u>11</u>He answered them,</p><h1>JOHN THE BAPTIST</h1><h4>He who has two coats, let him give to him who has none. He who has food, let him do likewise.</h4><p>   <u>12</u>Tax collectors also came to be baptized, and they said to him,</p><h1>THE TAX COLLECTORS</h1><h4>Teacher, what must we do?</h4><p>   <u>13</u>He said to them,</p><h1>JOHN THE BAPTIST</h1><h4>Collect no more than that which is appointed to you.</h4><p>   <u>14</u>Soldiers also asked him, saying,</p><h1>ROMAN SOLDIERS</h1><h4>What about us? What must we do?</h4><p>   He said to them,</p><h1>JOHN THE BAPTIST</h1><h4>Extort from no one by violence, neither accuse anyone wrongfully. Be content with your wages.</h4><p>   <u>15</u>As the people were in expectation, and all men reasoned in their hearts concerning John, whether perhaps he was the Christ, <u>16</u>John answered them all,</p><h1>JOHN THE BAPTIST</h1><h4>I indeed baptize you with water, but he comes who is mightier than I, the strap of whose sandals I am not worthy to loosen. He will baptize you in the Holy Spirit and fire, <u>17</u>whose fan is in his hand, and he will thoroughly cleanse his threshing floor, and will gather the wheat into his barn; but he will burn up the chaff with unquenchable fire.</h4><p>   <u>18</u>Then with many other exhortations he preached good news to the people, <u>19</u>but Herod the tetrarch,* being reproved by him for Herodias, his brother’s* wife, and for all the evil things which Herod had done, <u>20</u>added this also to them all, that he shut up John in prison. <u>21</u>Now when all the people were baptized, Jesus also had been baptized, and was praying. The sky was opened, <u>22</u>and the Holy Spirit descended in a bodily form like a dove on him; and a voice came out of the sky, saying</p><h1>GOD</h1><h2>You are my beloved Son. In you I am well pleased.</h2><p>   <u>23</u>Jesus himself, when he began to teach, was about thirty years old, being the son (as was supposed) of Joseph, the son of Heli, <u>24</u>the son of Matthat, the son of Levi, the son of Melchi, the son of Jannai, the son of Joseph, <u>25</u>the son of Mattathias, the son of Amos, the son of Nahum, the son of Esli, the son of Naggai, <u>26</u>the son of Maath, the son of Mattathias, the son of Semein, the son of Joseph, the son of Judah, <u>27</u>the son of Joanan, the son of Rhesa, the son of Zerubbabel, the son of Shealtiel, the son of Neri, <u>28</u>the son of Melchi, the son of Addi, the son of Cosam, the son of Elmodam, the son of Er, <u>29</u>the son of Jose, the son of Eliezer, the son of Jorim, the son of Matthat, the son of Levi, <u>30</u>the son of Simeon, the son of Judah, the son of Joseph, the son of Jonan, the son of Eliakim, <u>31</u>the son of Melea, the son of Menan, the son of Mattatha, the son of Nathan, the son of David, <u>32</u>the son of Jesse, the son of Obed, the son of Boaz, the son of Salmon, the son of Nahshon, <u>33</u>the son of Amminadab, the son of Aram,* the son of Hezron, the son of Perez, the son of Judah, <u>34</u>the son of Jacob, the son of Isaac, the son of Abraham, the son of Terah, the son of Nahor, <u>35</u>the son of Serug, the son of Reu, the son of Peleg, the son of Eber, the son of Shelah, <u>36</u>the son of Cainan, the son of Arphaxad, the son of Shem, the son of Noah, the son of Lamech, <u>37</u>the son of Methuselah, the son of Enoch, the son of Jared, the son of Mahalaleel, the son of Cainan, <u>38</u>the son of Enos, the son of Seth, the son of Adam, the son of God.<br />   <strong>4</strong><u>1</u>Jesus, full of the Holy Spirit, returned from the Jordan, and was led by the Spirit into the wilderness <u>2</u>for forty days, being tempted by the devil. He ate nothing in those days. Afterward, when they were completed, he was hungry. <u>3</u>The devil said to him,</p><h1>SATAN</h1><h4>If you are the Son of God, command this stone to become bread.</h4><p>   <u>4</u>Jesus answered him, saying,</p><h1>JESUS</h1><h2>It is written, ‘Man shall not live by bread alone, but by every word of God.’*</h2><p>   <u>5</u>The devil, leading him up on a high mountain, showed him all the kingdoms of the world in a moment of time. <u>6</u>The devil said to him,</p><h1>SATAN</h1><h4>I will give you all this authority, and their glory, for it has been delivered to me; and I give it to whomever I want. <u>7</u>If you therefore will worship before me, it will all be yours.</h4><p>   <u>8</u>Jesus answered him,</p><h1>JESUS</h1><h2>Get behind me Satan! For it is written, ‘You shall worship the Lord your God, and you shall serve him only.’*</h2><p>   <u>9</u>He led him to Jerusalem, and set him on the pinnacle of the temple, and said to him,</p><h1>SATAN</h1><h4>If you are the Son of God, cast yourself down from here, <u>10</u>for it is written,<br />   ‘He will put his angels in charge of you, to guard you;’<br />   <u>11</u>and,<br />   ‘On their hands they will bear you up,<br />   lest perhaps you dash your foot against a stone.’*</h4><p>   <u>12</u>Jesus answering, said to him,</p><h1>JESUS</h1><h2>It has been said, ‘You shall not tempt the Lord your God.’*</h2><p>   <u>13</u>When the devil had completed every temptation, he departed from him until another time.<br />   <u>14</u>Jesus returned in the power of the Spirit into Galilee, and news about him spread through all the surrounding area. <u>15</u>He taught in their synagogues, being glorified by all.<br />   <u>16</u>He came to Nazareth, where he had been brought up. He entered, as was his custom, into the synagogue on the Sabbath day, and stood up to read. <u>17</u>The book of the prophet Isaiah was handed to him. He opened the book, and found the place where it was written,</p><h1>JESUS</h1><h2><u>18</u>The Spirit of the Lord is on me,<br />   because he has anointed me to preach good news to the poor.<br />   He has sent me to heal the broken hearted,*<br />   to proclaim release to the captives,<br />   recovering of sight to the blind,<br />   to deliver those who are crushed,<br />   and to proclaim the acceptable year of the Lord.</h2><p>   <u>20</u>He closed the book, gave it back to the attendant, and sat down. The eyes of all in the synagogue were fastened on him. <u>21</u>He began to tell them,</p><h1>JESUS</h1><h2>Today, this Scripture has been fulfilled in your hearing.</h2><p>   <u>22</u>All testified about him, and wondered at the gracious words which proceeded out of his mouth, and they said,</p><h1>PEOPLE IN NAZARETH</h1><h4>Isn’t this Joseph’s son?</h4><p>   <u>23</u>He said to them,</p><h1>JESUS</h1><h2>Doubtless you will tell me this parable, ‘Physician, heal yourself! Whatever we have heard done at Capernaum, do also here in your hometown.’</h2><p><u>24</u>He said,</p><h1>JESUS</h1><h2>Most certainly I tell you, no prophet is acceptable in his hometown. <u>25</u>But truly I tell you, there were many widows in Israel in the days of Elijah, when the sky was shut up three years and six months, when a great famine came over all the land. <u>26</u>Elijah was sent to none of them, except to Zarephath, in the land of Sidon, to a woman who was a widow. <u>27</u>There were many lepers in Israel in the time of Elisha the prophet, yet not one of them was cleansed, except Naaman, the Syrian.</h2><p>   <u>28</u>They were all filled with wrath in the synagogue, as they heard these things. <u>29</u>They rose up, threw him out of the city, and led him to the brow of the hill that their city was built on, that they might throw him off the cliff. <u>30</u>But he, passing through the middle of them, went his way.<br />   <u>31</u>He came down to Capernaum, a city of Galilee. He was teaching them on the Sabbath day, <u>32</u>and they were astonished at his teaching, for his word was with authority. <u>33</u>In the synagogue there was a man who had a spirit of an unclean demon, and he cried out with a loud voice, <u>34</u>saying,</p><h1>A DEMON-POSSESSED MAN</h1><h4>Ah! what have we to do with you, Jesus of Nazareth? Have you come to destroy us? I know you who you are: the Holy One of God!</h4><p>   <u>35</u>Jesus rebuked him, saying,</p><h1>JESUS</h1><h2>Be silent, and come out of him!</h2><p>When the demon had thrown him down in the middle of them, he came out of him, having done him no harm.<br />   <u>36</u>Amazement came on all, and they spoke together, one with another, saying,</p><h1>PEOPLE IN CAPERNAUM</h1><h4>What is this word? For with authority and power he commands the unclean spirits, and they come out!</h4><p><u>37</u>News about him went out into every place of the surrounding region.<br />   <u>38</u>He rose up from the synagogue, and entered into Simon’s house. Simon’s mother-in-law was afflicted with a great fever, and they begged him for her. <u>39</u>He stood over her, and rebuked the fever; and it left her. Immediately she rose up and served them. <u>40</u>When the sun was setting, all those who had any sick with various diseases brought them to him; and he laid his hands on every one of them, and healed them. <u>41</u>Demons also came out of many, crying out, and saying,</p><h1>SOME DEMONS</h1><h4>You are the Christ, the Son of God!</h4><p>Rebuking them, he didn’t allow them to speak, because they knew that he was the Christ.<br />   <u>42</u>When it was day, he departed and went into an uninhabited place, and the multitudes looked for him, and came to him, and held on to him, so that he wouldn’t go away from them. <u>43</u>But he said to them,</p><h1>JESUS</h1><h2>I must preach the good news of God’s Kingdom to the other cities also. For this reason I have been sent.</h2><p><u>44</u>He was preaching in the synagogues of Galilee.</p><h5>*Luke 3:6 Isaiah 40:3-5<br />*Luke 3:19 a tetrarch is one of four governors of a province<br />*Luke 3:19 TR reads “brother Philip’s” instead of “brother’s”<br />*Luke 3:33 NU reads “Admin, the son of Arni” instead of “Aram”<br />*Luke 4:4 Deuteronomy 8:3<br />*Luke 4:8 Deuteronomy 6:13<br />*Luke 4:11 Psalm 91:11-12<br />*Luke 4:12 Deuteronomy 6:16<br />*Luke 4:18 NU omits “to heal the broken hearted”<br />*Luke 4:19 Isaiah 61:1-2</h5>","path":"/327","Question1":"Consider the acts of God described in this passage.","Question2":"What do these illustrate about the ways of God?","Question3":"Have you experienced these ways of God in your life? If yes, how so? If not, what can you do to experience this?","Question4":"What can you do to grow in your desire to know and follow God more intimately this week?","prevPath":"/326","nextPath":"/328"}},"pathContext":{}}

